import React, { useState } from "react";
import { QRCodeCanvas } from "qrcode.react";

const QrTestPage: React.FC = () => {
  const [url, setUrl] = useState<string>("https://tinhhoaviet.org.vn");

  return (
    <div
      style={{
        padding: "40px",
        fontFamily: "sans-serif",
        textAlign: "center",
      }}
    >
      <h1>QR Code Website Generator</h1>

      <p>Enter any website URL and the QR code will generate below.</p>

      <input
        type="text"
        value={url}
        onChange={(e) => setUrl(e.target.value)}
        placeholder="Enter website URL..."
        style={{
          width: "360px",
          padding: "10px",
          fontSize: "16px",
          border: "1px solid #ccc",
          borderRadius: "6px",
        }}
      />

      {url && (
        <div style={{ marginTop: 30 }}>
          <QRCodeCanvas
            value={url}
            size={240}
            bgColor="#ffffff"
            fgColor="#000000"
            includeMargin={true}
          />

          <p
            style={{
              marginTop: 12,
              color: "#d4aa00",
              fontWeight: "bold",
              fontSize: "14px",
            }}
          >
            Scan to open: {url}
          </p>
        </div>
      )}
    </div>
  );
};

export default QrTestPage;
